<template>
  <div class="live-demo">
    <h1 v-if="title" class="title is-3" :id="id">{{ title }}</h1>

    <div>
      <div class="tabs is-marginless">
        <ul>
          <li
            v-for="tabName of tabsWithContent"
            :key="tabName"
            :class="{ 'is-active': currentTab === tabName && !demoOnly }"
            @click="currentTab = tabName; demoOnly = false">
            <a>{{ tabName }}</a>
          </li>
          <li :class="{ 'is-active': demoOnly }" @click="demoOnly = true; currentTab = null"><a>Demo Only</a></li>
        </ul>
      </div>

      <div class="columns is-gapless live-demo__content">
        <div class="column is-two-thirds" v-show="!demoOnly">
          <template v-for="tabName of tabsWithContent">
            <keep-alive :key="tabName">
              <pre class="live-demo__code" :lang=tabName v-if="tabName === currentTab" :key="tabName"><code v-html="code[tabName]"/></pre>
            </keep-alive>
          </template>
        </div>
        <div ref="result" class="column is-inline-block live-demo__result"/>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import Prism from 'prismjs'

export default Vue.extend({
  props: {
    demoHtml: String,
    id: String,
    removeNext: Number
  },

  data () {
    const code = {
      JS: '',
      HTML: '',
      CSS: ''
    }

    const tabs = Object.keys(code)

    return {
      code,
      tabs,
      currentTab: null,
      title: '',
      htmlResult: '',
      demoOnly: false
    }
  },

  computed: {
    tabsWithContent () {
      return this.tabs.filter(tabName => !!this.code[tabName])
    }
  },

  methods: {
    parseDemo () {
      const demoDoc = new DOMParser().parseFromString(this.demoHtml, 'text/html')

      const body = demoDoc.querySelector('body')
      const style = demoDoc.querySelector('style')
      const script = demoDoc.querySelector('script')

      if (style) { this.$el.appendChild(style) }
      if (script) { script.remove() }

      for (const node of Array.from(body.childNodes)) {
        this.$refs.result.appendChild(node)
      }

      this.code = {
        JS: script && Prism.highlight(script.innerHTML.trim(), Prism.languages.javascript),
        CSS: style && Prism.highlight(style.innerHTML.trim(), Prism.languages.css),
        HTML: body && Prism.highlight(body.innerHTML.trim(), Prism.languages.markup)
      }

      this.title = demoDoc.title
      this.currentTab = this.tabsWithContent[0]

      // Try to run js code
      if (script) {
        script.remove()

        const evalScript = () => {
          try {
            if (script) {
              // eslint-disable-next-line no-new-func
              new Function(script.textContent)()
            }
          } catch (error) {
            /* eslint-disable no-console */
            console.error('livedemo failed to execute a javascript code block')
            console.log({ script, error })
            /* eslint-enable no-console */
          }
        }

        if (/interactive|complete/.test(document.readyState)) {
          evalScript()
        } else {
          document.addEventListener('DOMContentLoaded', evalScript)
        }
      }
    }
  },

  watch: {
    demoHtml () {
      this.parseDemo()
    }
  },

  mounted () {
    this.parseDemo()

    for (let i = 0; i < this.removeNext; i++) {
      this.$el.nextElementSibling.remove()
    }

    // Hide demo code if the screen is initially narrow
    if (window.innerWidth <= 570) {
      this.demoOnly = true
    }
  }
})
</script>
